import React, { Component } from 'react'
import Typewriter from 'typewriter-effect';
import {Link} from 'react-router-dom';
import { Button } from "shards-react";
import './style.css'

const content = "Y bueno.. Este es el fin, espero que te haya gustado este detalle que quise hacer para ti. Quiero ya verte pronto!"

const cont2 = "... Bueno... hay algo mas..."

export default class Part10 extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             classButton: 'notShowButton'
        }
    }

    handleShow = () =>{
        this.setState({classButton: 'showButton'})
    }
    
    render() {
        return (
            <div className="cont textContent">
            <Typewriter

                onInit={(typewriter) => {
                    typewriter.changeDelay(100)
                    .typeString(content)
                    .pauseFor(1500)
                    .typeString(cont2)
                    .callFunction(() => {
                        this.handleShow()
                    })
                    .start();
                }}
            />
            <div className={this.state.classButton}>
                <Link to='/letter/9'>
                    <Button className="buttonGeneral" theme="secondary">Anterior</Button>
                </Link>
                <Link to='/letter/11'>
                    <Button className="buttonGeneral" theme="secondary">Siguiente</Button>
                </Link>
            </div>
        </div>
        )
    }
}
