import React, { Component } from 'react'
import Typewriter from 'typewriter-effect';
import {Link} from 'react-router-dom';
import { Button } from "shards-react";
import './style.css'
import { Container, Row, Col } from "shards-react";

const cont1 = 'Ya quiero poder verte, seguro te mirare y quedare así...';
const cont2 = 'Y poder cogerte asi y darte muchos besos';
const cont3 = '...Te bañare asi';
const cont4 = 'Luego estar contigo en mis brazos...';
const cont5 = 'Y seguro esto pasará... Pero no importa, me gusta';
const cont6 = 'Y quiero luego salir y ver el atardecer contigo...';

//heart
const gif1 = "https://media.giphy.com/media/26xBRiIYbyjCzYMAU/giphy.gif"
//hug
const gif2 = 'https://media.giphy.com/media/q174RZ1Um4j72/giphy.gif'
//sunset
const gif3 = 'https://media1.tenor.com/images/0bc706b47363b5c769a6321bc398dc73/tenor.gif?itemid=16593630'
//mickey
const gif4 = 'https://media1.tenor.com/images/6054a6b139403e984baaec0d6628d5a3/tenor.gif?itemid=9331460'
//all my love
const gif5 = 'https://media.giphy.com/media/l4pTdcifPZLpDjL1e/giphy.gif';
// sanji
const gif6 = 'https://media.giphy.com/media/bOQeC2SNniMNy/giphy.gif';
// end message
const gif7 = 'https://media1.tenor.com/images/ff5fadb5e543e9b791749dddea553029/tenor.gif?itemid=9740268'
//bite
const gif8 = 'https://media1.tenor.com/images/2a680f8bfecd9e18d656de1629c14acc/tenor.gif?itemid=9447120'

export default class Part9 extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             classButton: 'notShowButton',
             pbarOut: 'notshowpbar',
             pbar: 'progressbar_p0',
             text2: null,
             index: 1,
             gifs:[gif6,gif4,gif5,gif2,gif8,gif3],
             gifSrc: gif6

        }
    }

    handleShow = () =>{
        this.setState({classButton: 'showButton'})
    }

    handleGif = async () =>{

        this.setState({
            gifSrc: this.state.gifs[this.state.index],
            index: this.state.index+1 
        })
    }

    render() {
        return (
            <div className="cont">
                <Container>
                    <Row>
                        <Col>
            <div className="textContent">
            <Typewriter
            onInit={(typewriter) => {
                typewriter.changeDelay(100)
                .changeDeleteSpeed(70)
                .typeString(cont1)
                .pauseFor(1500)
                .deleteAll()
                .callFunction(()=>{
                   this.handleGif()
                }).typeString(cont2)
                .pauseFor(1500)
                .deleteAll()
                .callFunction(()=>{
                   this.handleGif()
                }).typeString(cont3)
                .pauseFor(1500)
                .deleteAll()
                .callFunction(()=>{
                   this.handleGif()
                }).typeString(cont4)
                .pauseFor(1500)
                .deleteAll()
                .callFunction(()=>{
                   this.handleGif()
                }).typeString(cont5)
                .pauseFor(1500)
                .deleteAll()
                .callFunction(()=>{
                   this.handleGif()
                }).typeString(cont6)
                .pauseFor(1500)
                .callFunction(()=>{
                   this.handleShow()
                })
                .start();
            }}
        />
            </div>
            <div className={this.state.classButton}>
                <Link to='/letter/8'>
                    <Button className="buttonGeneral" theme="secondary">Anterior</Button>
                </Link>
                <Link to='/letter/10'>
                    <Button className="buttonGeneral" theme="secondary">Siguiente</Button>
                </Link>
            </div>
            </Col>
            <Col>
            <img className="imgGif" src={this.state.gifSrc} alt={this.state.gifSrc}></img>
            </Col>
            </Row>
            </Container>

            

        </div>
        )
    }
}
