import React, { Component } from 'react'
import Typewriter from 'typewriter-effect';
import { Link } from 'react-router-dom';
import { Button } from "shards-react";
import './style.css'
import { Container, Row, Col } from "shards-react";

const content = "jeje, Te amo 😘"
//heart
const gif1 = "https://media.giphy.com/media/26xBRiIYbyjCzYMAU/giphy.gif"
export default class Part11 extends Component {
    constructor(props) {
        super(props)

        this.state = {
            classButton: 'notShowButton'
        }
    }

    handleShow = () => {
        this.setState({ classButton: 'showButton' })
    }

    render() {
        return (
            <div className="cont">
                <Container>
                    <Row>
                        <Col>
                            <img className="imgGif2" src={gif1} alt={gif1}></img>
                        </Col>
                        <Col>
                            <div className="textContent" >
                                <Typewriter

                                    onInit={(typewriter) => {
                                        typewriter.changeDelay(100)
                                            .pauseFor(2000)
                                            .typeString(content)
                                            .pauseFor(500)
                                            .callFunction(() => {
                                                this.handleShow()
                                            })
                                            .start();
                                    }}
                                />
                            </div>

                            <div className={this.state.classButton}>
                                <Link to='/letter/'>
                                    <Button className="buttonGeneral" theme="secondary">Vuelta al inicio</Button>
                                </Link>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}
